$xs : 0;
$sm : 576px;
$md : 768px;
$lg : 992px;
$xl : 1200px;
$xxl: 1400px;
@import 'color.scss';
@import 'mixins.scss';

@media screen and (max-width: 1360px) {
    .sectionInner {
        width  : 100%;
        padding: 0 20px;
    }

    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(3, 230px);
    }

    .header .sectionInner {
        width        : 100%;
        padding-left : 20px;
        padding-right: 20px;
    }

    .sectionOffer_cardsWrapper_card {
        width: 23%;
    }

    .header_secondLine_searchField {
        width    : auto;
        flex-grow: 1;
    }

    .header_secondLine_btns {
        min-width  : 150px;
        flex-shrink: 0;
        flex-wrap  : nowrap;
    }

    .header_secondLine_callback {
        min-width: 145px;
    }

    .footer_firstLine_user {
        flex-grow   : 1;
        width       : auto;
        margin-right: 20px;
    }

    .footer_firstLine_catalog {
        flex-grow   : 2;
        width       : auto;
        margin-right: 20px;
    }

    .footer_firstLine_about {
        flex-grow   : 2;
        width       : auto;
        margin-right: 20px;

    }

    .header_secondLine_burger,
    .header_secondLine_callback,
    .header_secondLine_searchField {
        margin-right: 20px;
    }
}

@media screen and (max-width: 1175px) {
    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(2, 250px);
    }
}


@media screen and (max-width: 940px) {
    .header_secondLine_burger .burger-menu_active .burger-menu_nav {
        height        : auto;
        flex-direction: column;
        width         : 600px;
    }

    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(2, 1fr);
        margin               : 0 0 32px 0;
    }

    .header_secondLine_burger .burger_img {
        width: 100%;
    }

    .header_secondLine_callback_text,
    .phoneText,
    .header_secondLine_searchField_field {
        display: none;
    }

    .header_secondLine_logo {
        order: 1;
    }

    .header_secondLine_burger {
        order: 2;
    }

    .header_secondLine_btns {
        order  : 3;
        display: flex;
    }

    .header_secondLine_searchField {
        order       : 5;
        margin-right: 16px;
        width       : auto;

    }

    .header_secondLine_callback {
        order: 4;
    }

    .header_secondLine_personal {
        order: 6;
    }

    .header_secondLine_btns_item:last-child {
        margin-right: 24px;
    }

    .sectionBanner_banner {
        padding: 20px;
    }

    .sectionBanner_banner_title {
        font-size  : 56px;
        line-height: 64px;
    }

    .sectionOffer_cardsWrapper {
        flex-wrap: wrap;
    }

    .sectionOffer_cardsWrapper_card {
        margin        : 0 20px 20px 0;
        flex-direction: column-reverse;
        width         : 330px;
    }

    .sectionOffer_cardsWrapper_card_img {
        align-self: center;
    }

    .sectionCategory .sectionInner {
        flex-direction: column;
        padding       : 20px;
    }

    .sectionCategory_offer {
        width : 100%;
        height: 295px;
    }

    .sectionCategory_offer_label {
        top : 34px;
        left: 24px;
    }

    .sectionCategory_offer_price {
        bottom: auto;
        top   : 34px;
        right : 80px;
        width : 155px;
    }

    .sectionCategory_offer_img {
        height: 80%;
    }

    .footer_firstLine_logo {
        display: none;
    }

    .footer_firstLine .sectionInner span {
        flex-direction: column;
    }

    .footer_firstLine_logo,
    .footer_firstLine_user,
    .footer_firstLine_catalog,
    .footer_firstLine_about,
    .footer_firstLine_info {
        margin-bottom: 32px;
    }

    .footer_secondLine_copyright {
        padding: 0 24px 0 0;
    }

    .footer_secondLine {
        span {
            display       : flex;
            flex-direction: column;

            .footer_secondLine_link {
                margin: 0 0 12px 0;
            }
        }

    }

}

@media screen and (max-width: 740px) {
    .sectionOffer_cardsWrapper_card {
        width: 300px;
    }

    .header_secondLine_callback {
        min-width: auto;
    }

    .sectionBanner_banner {
        height: auto;
    }

    .sectionBanner_banner_tags {
        width      : 100%;
        display    : flex;
        align-items: flex-start;
        flex-wrap  : wrap;
    }

    .sectionBanner_banner_tags_item {
        white-space  : nowrap;
        width        : auto;
        margin-bottom: 20px;
    }

    .header_secondLine_personal {
        min-width: 100px;
    }
}

@media screen and (max-width: 690px) {
    .header_secondLine_burger .burger-menu_button span {
        display: none;
    }

    .sectionOffer_cardsWrapper_card {
        width: 260px;
    }
}

@media screen and (max-width: 600px) {
    .header_secondLine_burger .burger-menu_active .burger-menu_nav {
        height        : auto;
        flex-direction: column;
        width         : 480px;
    }

    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(2, 1fr);
        margin               : 0 0 32px 0;
    }

    .header_secondLine_btns {
        min-width: auto;
    }
}

@media screen and (max-width: 500px) {
    .header_secondLine_burger .burger-menu_active .burger-menu_nav {
        width: 300px;
    }

    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(1, 1fr);
        margin               : 0 0 32px 0;
    }
}

@media screen and (max-width: 400px) {
    .header_secondLine_burger .burger-menu_active .burger-menu_nav {
        height        : auto;
        flex-direction: column;
        width         : 300px;
    }

    .header_secondLine_burger .categoryContainer {
        grid-template-columns: repeat(1, 1fr);
        margin               : 0 0 32px 0;
    }
}

@media screen and (max-width: 580px) {

    .sectionPopular_line_sliderBtns,
    .sectionNews_line_sliderBtns {
        display: none;
    }

    .header_secondLine_burger .burger-menu_active .burger-menu_nav {
        left : auto;
        right: 0;
    }

    .header_firstLine_links_item {
        margin-right: 24px;
    }

    .header_firstLine_feedback_item {
        margin-right: 16px;

        &.phone {
            &::before {
                content   : '';
                display   : block;
                width     : 18px;
                height    : 18px;
                background: transparent url(../img/phone.svg) 50% 50%/cover;
                margin    : 0 4px 0 0;
            }
        }

        span {
            display: none;
        }
    }

    .header_secondLine_logo {
        margin-right: 0px;
        order       : 1;
    }

    .burger-menu_button {
        span {
            display: none;
        }
    }

    .header_secondLine_btns {
        order    : 2;
        flex-grow: 1;
    }

    .header_secondLine_burger {
        order: 4
    }

    .header_secondLine_personal,
    .header_secondLine_callback,
    .header_secondLine_searchField_field {
        display: none;
    }

    .header_secondLine_searchField {
        order: 3;
        width: auto;
    }

    .header_secondLine_btns_item {
        margin-right: 20px;
    }

    .header_secondLine_searchField {
        margin-right: 8px;
    }

    .header_secondLine_burger {
        margin: 0;
    }

    .sectionBanner_banner_tags {
        display       : flex;
        flex-direction: column;
        align-items   : flex-start
    }

    .sectionBanner_banner {
        padding: 20px;
        height : auto;
    }

    .sectionBanner_banner_tags_item {
        width      : auto;
        margin     : 0 0 4px 0;
        font-size  : 16px;
        line-height: 24px;
    }

    .sectionBanner_banner_title {
        font-size  : 32px;
        line-height: 40px;
    }

    .sectionBanner_banner_subTitle {
        font-size  : 16px;
        line-height: 24px;
    }

    .title {
        font-size  : 40px;
        line-height: 48px;
    }

    .sectionOffer_cardsWrapper {
        flex-direction : column;
        align-items    : center;
        justify-content: space-between;

    }

    .sectionOffer_cardsWrapper_card {
        margin        : 0 0 20px 0;
        width         : 100%;
        flex-direction: column-reverse;
    }

    .sectionOffer_cardsWrapper_card_img {
        align-self: center;
    }

    .sectionOffer_text {
        line-height: 24px;
        font-size  : 20px;
    }

    .sectionCategory .sectionInner {
        flex-direction: column;
        padding       : 20px;
    }

    .sectionCategory_offer {
        width : 100%;
        height: 295px;
    }

    .sectionCategory_offer_label {
        top : 18px;
        left: 20px;
    }

    .sectionCategory_offer_price {
        bottom: 20px;
        right : 20px;
        top   : auto;
        width : 155px;
    }

    .sectionCategory_offer_img {
        height: 80%;
    }

    .footer_firstLine .sectionInner {
        flex-direction: column;
    }

    .footer_firstLine_logo,
    .footer_firstLine_user,
    .footer_firstLine_catalog,
    .footer_firstLine_about,
    .footer_firstLine_info {
        display      : flex;
        margin-bottom: 32px;
    }

    .footer_firstLine_info {
        align-items: flex-start;
    }

    .footer_secondLine .sectionInner {
        flex-direction : column;
        align-items    : flex-start;
        justify-content: flex-start;
    }

    .footer_secondLine_copyright {
        width  : 100%;
        padding: 0;
        margin : 0 0 16px 0;
    }

    .footer_secondLine_link {
        margin-bottom: 12px;
    }

    .footer_firstLine {
        margin-bottom: 48px;
    }
}